import React                                        from 'react';
import PropTypes                                    from 'prop-types';

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import LinearProgress                               from 'material-ui/LinearProgress';


/* ################################################### */
/*          constant         */
/* ################################################### */
import { ServiceEnumHTTP }                          from '../../../_service/helper/service.enum.http';


/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../_abstract/c.abstract';

import '../s.onlineTC.css';


export default class ComponentOnlineTCDocUpload extends ComponentAbstract {


    static propTypes = {
        formDoc                    : PropTypes.object

        , isDisableUpload       : PropTypes.bool

        , onClickReset          : PropTypes.func
        , onSelectFile          : PropTypes.func
        , onClickUpload         : PropTypes.func
    }


    static defaultProps = {
        isDisableUpload           : false

        , onClickReset              : () => {}
        , onSelectFile              : () => {}
        , onClickUpload             : () => {}
    }


    constructor( props ) {
        super( props )
    }


    resetForm() {
        console.log( 'ComponentOnlineTCDocUpload : resetForm : *** ' );

        this.refs.inputForm.reset();
    }


    bytesToSize( bytes ) {
        var sizes = [ 'Bytes' , 'KB' , 'MB' , 'GB' , 'TB' ];
        if ( bytes == 0 ) return '0 Byte';
        var i = parseInt( Math.floor( Math.log( bytes ) / Math.log( 1024 ) ) );
        return Math.round( bytes / Math.pow( 1024 , i ) , 2 ) + ' ' + sizes[ i ];
    }

    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
            <div>

                 <form
                        ref={ 'inputForm' }>
                    <input
                        ref={ 'inputUpload' }

                        type='file'
                        multiple={ false }
                        style={ { display : 'none' } }

                        onChange={ ( event ) => {
                            this.props.onSelectFile( event.target.files.length == 0 ? null : event.target.files[ 0 ] );
                        } }
                    />
                </form>

                {
                    this.props.isDisableUpload
                    ?
                        ( <LinearProgress mode="determinate" value={ this.props.formDoc.fileCompletePercentage } /> )
                    :
                        ( null )
                }


                <div>

                        <RaisedButton
                                label={ this.props.formDoc.file == null ? 'Browse file' : ( this.props.formDoc.file.name +  ' (' + this.bytesToSize( this.props.formDoc.file.size ) + ')' ) }
                                primary={ this.props.formDoc.file != null }
                                disabled={ this.props.isDisableUpload }
                                className={ ' onlineTC-upload-document-margin-20 ' }
                                onClick={ () => {
                                    this.refs.inputUpload.click();
                                } } />

                        <RaisedButton
                                label={ 'Upload' }
                                className={ ' onlineTC-upload-document-margin-20 ' }
                                disabled={ this.props.isDisableUpload ? true : this.props.formDoc.file == null }
                                onClick={ () => {
                                    this.props.onClickUpload();
                            } } />  

                        <RaisedButton
                                className={ ' onlineTC-upload-document-margin-20 ' }
                                label={ 'Cancel' }
                                disabled={ this.props.isDisableUpload }
                                onClick={ () => {
                                    this.resetForm();
                                    this.props.onClickReset();
                            } } />

                </div>
            </div>
        );
    };
}


