import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Step,
    Stepper,
    StepLabel,
  }                                                   from 'material-ui/Stepper';

import Paper                                        from 'material-ui/Paper';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionOnlineTCDetail , { ReduxEnumOnlineTCDetail } from '../../../_redux/action/reduxAction.onlineTC.detail';







/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';









/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';
import ComponentOnlineTCDetailStep1                      from './c.onlineTC.detail.step1';
import ComponentOnlineTCDetailStep2                      from './c.onlineTC.detail.step2';
import ComponentOnlineTCDetailStep3                      from './c.onlineTC.detail.step3';
import ComponentCustomAlert                         from '../../_component/alert/cc.alert';










/* ################################################### */
/* 		 util		 */
/* ################################################### */

import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';

import { StringUtil }                               from '../../../_util/util.string';
import FormValidateUtil                             from '../../../_util/util.formValidate';

import '../s.onlineTC.css';










export default connect(
        ( state ) => {
            let thisState = state[ 'ReduxOnlineTCDetail' ] ;
                let stateObj = { };

                if ( thisState ) {

                    for ( let i = 0 ; i < thisState.length; i ++ ) {
                        let recordItem = thisState[ i ];
                        if ( !recordItem[ 'completed' ] ) {


                            if ( ReduxEnumOnlineTCDetail.INIT_DONE === recordItem[ 'type' ] ) {
                                // stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                                // stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                            } else if ( ReduxEnumOnlineTCDetail.INSERT_DONE === recordItem[ 'type' ] ) {
                                stateObj[ 'onReadyFunction' ]       = 'requestInsertDone';
                                stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                            } else if ( ReduxEnumOnlineTCDetail.CONFIRM_INSERT_DONE === recordItem[ 'type' ] ) {
                                stateObj[ 'onReadyFunction' ]       = 'requestConfirmInsertDone';
                                stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                            } else if ( ReduxEnumOnlineTCDetail.DELETE_DONE === recordItem[ 'type' ] ) {
                                stateObj[ 'onReadyFunction' ]       = 'requestDeleteDone';
                                stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                            } else if ( ReduxEnumOnlineTCDetail.CONFIRM_DELETE_DONE === recordItem[ 'type' ] ) {
                                stateObj[ 'onReadyFunction' ]       = 'requestConfirmDeleteDone';
                                stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                            }


                            recordItem[ 'completed' ] = true;
                        }
                    }

                }

                return stateObj;
        }
    ) (
        class ComponentOnlineTCDetail extends ComponentAbstract {

            constructor( props ) {
                super( props );

                console.log( 'ComponentOnlineTCDetail : constructor : *** : [' + this.uuid() + '] ' , props );



                this.state = {
                    step : 0

                    , isLoading : false

                    , result : {}

                    , form : {
                        tncType : ''
                        , tncTypeName: ''
                        , tncEffectiveDate : ''
                        , documentId : ''
                        , item : props['location']['state']['itemToRemove']
                        , TNC_TYPE : [
                            { id: 1 , name: "General"  }
                            , { id: 2 , name: "Fund Transfer"  }
                            , { id: 3 , name: "IBG"  }
                            , { id: 4 , name: "FTT"  }
                            , { id: 5 , name: "Bulk Payment"  }
                            , { id: 6 , name: "Payroll"  }
                            , { id: 7 , name: "KWSP"  }
                            , { id: 8 , name: "SOCSO"  }
                            , { id: 9 , name: "LHDN"  }
                            , { id: 10 , name: "Overall"  }
                            , { id: 11 , name: "Multiple Approval"  }
                        ]
                    }
                    , isDisableUpload : false
                    , formDoc : {
                        file                      : null
                        , fileCompletePercentage    : 0
                    }

                    , mode : {
                        status : props['location']['state']['mode_status']
                        , statusHeader : props['location']['state']['mode_status_header']
                    }

                    , formValidation : {}

                }
            }

            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */
           _onReady() {
                if (this.state.mode.status === 'remove') {
                    this.requestDelete();
                }
            }

            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */
           onClickBack( ) {
                console.log( 'ComponentOnlineTCDetail : onClickBack : *** ' );

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.ONLINE_TC_LIST )
                );
            }

            onClickCancelPreviewStep( ) {
                console.log( 'ComponentOnlineTCDetail : onClickCancelPreviewStep : *** ' );
                this.requestStep(0);
            }

            onClickCancelConfirmStep( ) {
                console.log( 'ComponentOnlineTCDetail : onClickCancelConfirmationStep : *** ' );
                this.requestStep(1);
            }

            requestStep( step ) {
                this.setState( {
                    step     : step
                } );
            }

            onClickPreview() {
                FormValidateUtil.validate(
                    this.state.form
                    , {
                        tncType           : { required : true }
                        , tncEffectiveDate    : { required : true }
                    }
                ).then(
                    ( value ) => {
                        this.requestInsert();
                    }
                ).catch(
                    ( error ) => {
                        this.setState( { formValidation : error } );
                    }
                );
            }

            onClickConfirm( ) {
                console.log( 'ComponentOnlineTCDetail : onClickConfirm : *** ' );

                if (this.state.mode.status === 'add') {
                    this.requestConfirmInsert();
                } else {
                    this.requestConfirmDelete();
                }
            }

            onClickSubmit( ) {
                console.log( 'ComponentOnlineTCDetail : onClickSubmit : *** ' );

                this.requestConfirmInsert();
            }











             /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
            */
            requestInsert() {
                console.log( 'ComponentOnlineTCDetail : requestInsert : *** ' );


                        Promise.all( [
                            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                        ] ).then(
                            ( value ) => {

                                this.setState(
                                    { isLoading : true }
                                    , () => {
                                        this.dispatch(
                                            ReduxActionOnlineTCDetail.rInsert(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , { tncType : this.state.form.tncType
                                                    , tncEffectiveDate : this.state.form.tncEffectiveDate
                                                    , documentId : this.state.form.documentId
                                                }
                                            )
                                        )
                                    }
                                );

                            }
                        );
            }
            requestInsertDone( result ) {
                console.log( 'ComponentOnlineTCDetail : requestInsertDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionOnlineTCDetail.rRequestEnd()
                );

                this.requestStep(1);
            }

            requestConfirmInsert( instructionId ) {
                console.log( 'ComponentOnlineTCDetail : requestConfirmInsert : *** : instructionId => ' , instructionId );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionOnlineTCDetail.rConfirmInsert(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] },
                                        { instructionId : this.state.result['instructionId'] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestConfirmInsertDone( result ) {
                console.log( 'ComponentOnlineTCDetail : requestConfirmInsertDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionOnlineTCDetail.rRequestEnd()
                );

                this.requestStep(2);
            }

            requestDelete() {
                console.log( 'ComponentOnlineTCDetail : requestDelete : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionOnlineTCDetail.rDelete(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { tncId : this.state.form.item.tncId }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestDeleteDone( result ) {
                console.log( 'ComponentOnlineTCDetail : requestDeleteDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionOnlineTCDetail.rRequestEnd()
                );

                this.requestStep(1);
            }

            requestConfirmDelete( instructionId ) {
                console.log( 'ComponentOnlineTCDetail : requestConfirmDelete : *** : instructionId => ' , instructionId );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionOnlineTCDetail.rConfirmDelete(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] },
                                        { instructionId : this.state.result['instructionId'] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestConfirmDeleteDone( result ) {
                console.log( 'ComponentOnlineTCDetail : requestConfirmDeleteDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionOnlineTCDetail.rRequestEnd()
                );
                this.requestStep(2);
            }







             /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
            */
            render() {
                return (
                    <div>

                        <Paper style={ { width: '70%', margin: '0px auto' } }>

                            <div style = { { boxShadow: '0 4px 2px -2px #eee', paddingBottom: 2 } }>
                                <Stepper 
                                    style={ { width: '80%', margin: '20px auto', paddingTop: '25px' } }
                                    activeStep={ this.state.step }>

                                    <Step>
                                        <StepLabel>
                                            { this.state.mode.statusHeader }
                                        </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Preview </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Acknowledgement </StepLabel>
                                    </Step>

                                </Stepper>
                            </div>


                            <div style={ { margin: '0px auto', width: '75%' } }>

                                  {
                                        ( () => {

                                            switch ( this.state.step ) {

                                                case 0:
                                                    return (
                                                        <ComponentOnlineTCDetailStep1
                                                                    isDisableUpload={ this.state.isDisableUpload }
                                                                    form={ this.state.form }

                                                                    formValidation={ this.state.formValidation }

                                                                    onChangeForm={ ( key , value ) => {
                                                                        console.log( 'ComponentOnlineTCDetail : change form value => ' , value );

                                                                        let form = this.state.form;
                                                                        form[ key ] = value;
                                                                        this.setState( { form : form } );
                                                                    } }
                                                                    onClickPreview={ () => {
                                                                        this.onClickPreview();
                                                                    } }
                                                                    onClickCancelCreate={ () => {
                                                                        this.onClickBack();
                                                                    } }
                                                                    onClickReset={ () => {
                                                                        this.onClickReset();
                                                                    } } 
                                                                    onSelectFile={ ( file ) => {
                                                                        this.onSelectFile( file );
                                                                    } }
                                                                    onClickUpload={ () => {
                                                                        this.onClickUpload();
                                                                    } }
                                                        />
                                                    );
                                                    break;

                                                case 1:
                                                return (
                                                    <ComponentOnlineTCDetailStep2
                                                                form={ this.state.form }
                                                                mode={ this.state.mode }
                                                                onClickConfirm={ () => {
                                                                    this.onClickConfirm();
                                                                } }
                                                                onClickCancelPreview={
                                                                    this.state.mode.status === 'add'
                                                                    ?
                                                                    (() => { this.onClickCancelPreviewStep(); })
                                                                    :
                                                                    (() => { this.onClickBack(); })
                                                                }
                                                    />
                                                );
                                                    break;

                                                case 2:

                                                return (
                                                    <ComponentOnlineTCDetailStep3
                                                                mode={ this.state.mode }
                                                    />
                                                );

                                                    break;

                                                default:
                                                    return null;

                                            }

                                        } )()
                                    }

                            </div>

                        <ComponentCustomLoading
                                        visible={ this.state.isLoading }
                        />

                    </Paper>

                </div>
                );
            }
        }
    )



