import React                                        from 'react';
import PropTypes                                    from 'prop-types';

/* ################################################### */
/*          ui framework         */
/* ################################################### */

import RaisedButton                                 from 'material-ui/RaisedButton';

/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../_abstract/c.abstract';

import '../s.onlineTC.css';

export default class ComponentOnlineTCDetailStep3 extends ComponentAbstract {
    static propTypes = {
        mode        : PropTypes.object
    }

    static defaultProps = {
        mode : {}
    }

    constructor( props ) {
        super( props )
    }

     /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
   render() {
        return (
            <div >
                    <div className='onlineTC-detail-form-step3'>
                        {
                           this.props.mode.status == 'add' ? 
                           (

                            <h4>Terms and Conditions is successfully added</h4>
                        
                            ) : (

                            <h4>Terms and Condition is successfully removed</h4>

                            )
                        }
                    </div>

            </div>
        );
    }
}


